@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Roboto:wght@300&display=swap);
:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary{
    background-color: #fff;
    background-color: var(--primary);
    color:black;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, 
.btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}
*{
  box-sizing: border-box;


}

.navbar {
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    font-size: 1.2rem;
    position: fixed;
    left: 0;
    top:0;
    width: 100%;   
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:100%;
  }
  
  .logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  
  .fa-brands {
    margin-left: 0.5rem;
    font-size: 1.8rem;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {  
    
   .nav-menu{
    display:none;
   }
   .nav-menu.active{
    display:block;
    background: #242222;
    position: absolute;
    width:100%;
    top: 70px;
    margin: 10px 0;
    height:100vh;
    padding: 0;
   }
   .nav-item{
    margin:20px;
   }
   .nav-links{
    display: flex;
    justify-content: center;
    align-items: center;
    padding:2rem;
  
   }

   .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 4px;
  }
   
   .logo{
    position:absolute;
    left: 50px;
   }
   .menu-icon{
    display: block;
    position: absolute;
    right:50px;
   }
   .fa-bars{
    font-size: 2rem;

   }
   .fa-x{
    font-size: 1.5rem;
    color: #fff;
   }
   .nav-links-mobile {
    display: block;
    text-align: center;
    margin: auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }
  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  
}
